.skills-container{
    margin: 4rem 0;
    position: relative;
}

.skills-container::after,
.skills-container::before{
    content: " ";
    width: 28rem;
    height: 28rem;
    border-radius: 28rem;
    background: #14467c;
    position: absolute;
    z-index: -1;
    filter: blur(200px);
}

.skills-container::after{
    top: -3rem;
    left: 3rem;
}

.skills-container::before{
    background: #00ADEF;
    bottom: 0rem;
    right: 0rem;
}

.skills-container h5{
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 3.5rem;
}

.skills-content {
    display: flex;
    align-items: flex-start;
    gap: 3rem;
}

.skills{
    flex: 1;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
}

.skills-info{
    flex: 1
}

@media (max-width: 1025px){
    .skills-container{
        max-width: 900px;
        margin: 3rem auto;
    }

    .skills{
        padding-left: 1rem;
    }

    .skills, .skills-content{
        grid-gap: 2rem;
    }
}

@media (max-width: 768px){
    .skills-container{
        max-width: 650px;
        margin: 3rem auto;
    }

    .skills-content{
        flex-direction: column-reverse;
        gap: 3rem;
    }

    .skills, .skills-info{
        width: 100%;
    }

    .skills-container h5{
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }

    .skills-container::after,
    .skills-container::before{
        width: 18rem;
        height: 18rem;
    }
}

@media (max-width: 600px){
    .skills-container{
        padding: 0;
        margin: auto;
    }
}

@media (max-width: 426px){
    .skills-container{
        max-width: 370px;
        margin: 1rem auto;
    }
}

